      h1 Indicadores financieros y de gestin
    

    p.mb-4 Las empresas se encuentran en constantes cambios por las variaciones generadas en el mercado; por lo tanto, las actividades deben ser modificadas conforme con los requerimientos y necesidades. Asimismo, se realiza el  monitoreo y el seguimiento frecuentemente, por ello, para medir los resultados se disean e implementan indicadores que sean tiles y comprensibles para tomar decisiones que contribuyan al logro de los objetivos organizacionales.


    separador
    #t_2_1.titulo-segundo
      .h2 2.1 Indicadores de gestin
    
    .bloque-texto-a.reverse.color-primario.p-4.mb-5 
      .row.m-0.align-items-center.justify-content-between
        .col-lg-9
          .bloque-texto-a__texto.p-5
            p Los indicadores permiten que la gestin de las organizaciones sea efectiva y eficiente, puesto que facilita a los integrantes mejorar su desempeo, permitindoles evaluar la gestin y mejorar los niveles de aprendizaje en la organizacin.
        .col-lg-3.mb-4.mb-lg-0
          figure
            img.mx-auto(src="@/assets/curso/tema2/img1.svg", alt="imagen ambiental" style='width:250px;')
              

    AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--gris")
      .row.justify-content-center(titulo="Concepto")
        .col-md-12.mb-4.mb-md-0.ps-lg-5
          p.mb-5 Un indicador es una medicin cuantitativa o cualitativa, cuyo objetivo es verificar el cumplimiento de objetivos especficos. Los indicadores de gestin miden el cumplimiento de los objetivos estratgicos de la organizacin, teniendo en cuenta lo sealado en la planeacin estratgica. Se enfocan en la medicin del desempeo, eficiencia, eficacia, efectividad y productividad de la empresa, buscando la mejora continua de los procesos y procedimientos. Cabe resaltar que los indicadores se disean por las diferentes reas de la organizacin de acuerdo con las actividades que se desean medir de forma cuantitativa o cualitativa (Roncancio, 2019).
          p.mb-4 #[strong Metodologa para definir indicadores]
          p.mb-4 A continuacin se describen los pasos a seguir: 
          .row.justify-content-center.mb-5
            .col-lg-7
              ul.lista-ul
                li 
                  i.fas.fa-chevron-right
                  p.mb-0 Definir objetivos y estrategias: para crear el indicador es importante saber qu es lo que se va a medir y qu propsito cumple dentro de la organizacin.
                li 
                  i.fas.fa-chevron-right
                  p.mb-0 Precisar los Factores Crticos de xito (FCE): son las variables internas y externas que contribuyan al logro o no de los objetivos.
                li 
                  i.fas.fa-chevron-right
                  p.mb-0 Relacionar el indicador para cada factor clave de xito: su objetivo es monitorear las actividades para cumplir con los objetivos.
                li 
                  i.fas.fa-chevron-right
                  p.mb-0 Identificar el tipo de escala valorativa: se utiliza para medir resultados (estado, umbral y rango de gestin).
                    ul.mt-2
                      li  
                        i.fas.fa-chevron-right
                        p.mb-0 Estado: valor inicial de un indicador.
                      li  
                        i.fas.fa-chevron-right
                        p.mb-0 Umbral: indicador que se quiere lograr.
                      li  
                        i.fas.fa-chevron-right
                        p.mb-0 Rango de gestin: espacio comprendido entre los valores mnimos y mximos aceptables del indicador.
                li 
                  i.fas.fa-chevron-right
                  p.mb-0 Debe ser confiable y fcil de medir: la informacin debe ser correcta, fiable y verdica, utilizando mtodos y herramientas disponibles. Se asignan responsables de la recoleccin, tabulacin, anlisis y presentacin de la informacin.
                li 
                  i.fas.fa-chevron-right
                  p.mb-0 Establecer y asignar recursos: de acuerdo con las necesidades y objetivos propuestos.
                li 
                  i.fas.fa-chevron-right
                  p.mb-0 Medir y ajustar: debe ser preciso, vlido y oportuno. Se define de forma clara, que apunte a los objetivos propuestos y sea oportuno para la toma de decisiones.
                li 
                  i.fas.fa-chevron-right
                  p.mb-0 Estandarizar y formalizar: es la etapa de documentacin, divulgacin y conocimiento de los indicadores de gestin.
          
          p.mb-5
            b Los indicadores poseen los siguientes elementos:
          .row.justify-content-center.mb-5
            .col-lg-7
              ul.lista-ul
                li 
                  i.fas.fa-chevron-right
                  p.mb-0 Nombre: debe ser claro y concreto.
                li 
                  i.fas.fa-chevron-right
                  p.mb-0 Frmula: para calcular el indicador.
                li 
                  i.fas.fa-chevron-right
                  p.mb-0 Escala de medida: la forma como se presenta el resultado, por ejemplo, en porcentajes, unidades, etc.
                li 
                  i.fas.fa-chevron-right
                  p.mb-0 Definicin: expresa el concepto del indicador, lo que mide para el cumplimiento de los objetivos.
          
          p.mb-5
            b Ventajas de los indicadores de gestin:
          .row.justify-content-center.mb-5
            .col-lg-7
              ul.lista-ul
                li 
                  i.fas.fa-chevron-right
                  p.mb-0 Incentiva a los colaboradores a trabajar continuamente por el cumplimiento de los objetivos.
                li 
                  i.fas.fa-chevron-right
                  p.mb-0 Permite la mejora continua.
                li 
                  i.fas.fa-chevron-right
                  p.mb-0 Fortalece el trabajo en equipo.
                li 
                  i.fas.fa-chevron-right
                  p.mb-0 Motiva a desarrollar las actividades en trminos de eficiencia, eficacia y productividad.
                li 
                  i.fas.fa-chevron-right
                  p.mb-0 Permite realizar comparativos para el diseo e implementacin de estrategias acorde con la planeacin estratgica.
                li 
                  i.fas.fa-chevron-right
                  p.mb-0 Presenta informacin oportuna para la toma de decisiones.
      
      .row.justify-content-center(titulo="Clasificacin")
        .col-md-12.mb-4.mb-md-0.ps-lg-5
