                .tarjeta.tarjeta--azul(style='border-right: solid 5px #d9a171;')
                  .p-4
                    h3 Permanentes
                    p Son indicadores que se disean para actividades especficas dentro de la organizacin, que son constantes y permanecen en el tiempo.
          p.mb-4
            b Segn el nivel de generacin y utilizacin:
          p.mb-5 Los siguientes indicadores se encuentran relacionados con la organizacin y planeacin estratgica para el cumplimiento de los objetivos propuestos. Los resultados obtenidos son el insumo para tomar decisiones:
          .row.justify-content-center
