            p Para aplicar el procedimiento de clculo de los indicadores financieros es importante tener los estados financieros de dos o ms perodos, lo que permitir analizar y observar las debilidades y fortalezas de la empresa en trminos monetarios y porcentuales, con el fin de disear e implementar acciones de mejora.


    h4.mb-4 Concepto
    p.mb-4 Los indicadores o razones financieras pertenecen a la metodologa de anlisis financiero, cuyo objetivo es medir y cuantificar la informacin para la adecuada toma de decisiones en inversin, rentabilidad, liquidez y endeudamiento.
    h4.mb-4 Clasificacin
    .row.mb-4
      .col-lg-8
        .cajon.color-acento-contenido.p-4.mb-4
          p.mb-0 Los estados financieros reflejan la realidad econmica y financiera de la empresa; sin embargo, para realizar el anlisis detallado, comprensible y til se requiere comparar dos o ms perodos, por ello, la importancia de aplicar los indicadores financieros. Estos permiten verificar la situacin financiera de la empresa en un perodo determinado, as mismo, validar las proyecciones realizadas, comparando lo presupuestado con lo ejecutado, constatando el cumplimiento de los objetivos financieros.
        p.mb-4 Los indicadores que se utilizan con mayor frecuencia son los siguientes:
        ul.lista-ul.ms-5.mb-4
          li 
            i.fas.fa-chevron-right
            p.mb-0 Razones de liquidez.
          li 
            i.fas.fa-chevron-right
            p.mb-0 Razones de actividad.
          li 
            i.fas.fa-chevron-right
            p.mb-0 Razones de endeudamiento o apalancamiento.
          li 
            i.fas.fa-chevron-right
            p.mb-0 Razones de rentabilidad.
          li 
            i.fas.fa-chevron-right
            p.mb-0 Razones de valor de mercado de la empresa.  (Martnez, s.f., p.1)
