    AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--gris")
      .row.justify-content-center(titulo= "Indicadores de liquidez")
        .col-md-12.mb-4.ps-lg-5
          p.mb-4 Los indicadores de liquidez miden la capacidad de la empresa para asumir las obligaciones a corto plazo (un ao). En el clculo intervienen los activos y pasivos corrientes que se encuentran relacionados en el estado de situacin financiera.
          p.mb-4 Los indicadores de liquidez ms utilizados son los siguientes:
          h4.mb-4 Razn corriente
          p.mb-4 El indicador de razn corriente o razn circulante mide la capacidad que tiene la empresa para cubrir las obligaciones a corto plazo; para ello, verifique cuntos pesos del activo corriente tiene disponibles para respaldar dichas deudas inferiores a un ao.
          p.mb-4 Frmula de razn corriente:
          .row.mb-4(style='justify-content: center;')
            .col-lg-4
              figure.mb-4
                img(src='@/assets/curso/tema2/img18.svg', alt='imagen ambiental').mx-auto
            .col-lg-7
              figure.mb-4
                img(src='@/assets/curso/tema2/formula3.svg', alt='Razn corriente', style='width:540px').mx-auto
          .cajon.color-acento-contenido.p-4.mb-5
            p.mb-0 #[strong Interpretacin:] la empresa tiene una razn corriente de 1,23 en el ao 2019, es decir, que por cada peso que la empresa debe en el corto plazo cuenta con $1,23 del activo corriente para respaldar la obligacin.
          h4.mb-4 Capital neto de trabajo
          p.mb-4 El capital de trabajo neto es el indicador que muestra la disponibilidad del activo corriente para asumir los compromisos de la empresa y operar en condiciones normales, es decir, adquirir materias primas, pagar servicios, arrendamientos, sueldos, entre otros (Gerencie, 2020).
          p.mb-4 Frmula capital neto de trabajo:
          .row.mb-4(style='justify-content: center;')
            .col-lg-7
              figure.mb-4
                img(src='@/assets/curso/tema2/formula4.svg', alt='Capital neto de trabajo', style='width:640px').mx-auto
            .col-lg-4
              figure.mb-4
                img(src='@/assets/curso/tema2/img19.svg', alt='imagen ambiental', style='width:240px;').mx-auto
          .cajon.color-acento-contenido.p-4.mb-5
            p.mb-0 #[strong Interpretacin:] la empresa Contable SAS tiene disponible $27.003.200 para cumplir con sus compromisos y operar normalmente.
          
          h4.mb-4 Prueba cida
          p.mb-4 El indicador de prueba o razn cida mide la capacidad que tiene la empresa para cubrir las obligaciones a corto plazo, sin tener que recurrir a la venta de sus inventarios y sin tener en cuenta los gastos pagados por anticipado. Tambin se conoce con el nombre de liquidez seca, incluye los saldos de efectivo, cuentas por cobrar e inversiones temporales.
          p.mb-4  Frmula prueba cida:
          .row.mb-4(style='justify-content: center;')
            .col-lg-5
              figure.mb-4
                img(src='@/assets/curso/tema2/img20.png', alt='imagen ambiental', style='width:480px;').mx-auto
            .col-lg-7
              figure.mb-4
                img(src='@/assets/curso/tema2/formula5.svg', alt='', style='width:540px').mx-auto
          .cajon.color-acento-contenido.p-4.mb-5
            p.mb-0 #[strong Interpretacin: la empresa Contable SAS tiene disponible $0,49 centavos para respaldar las obligaciones a corto plazo, sin tener que vender sus inventarios.] 
      

      .row.justify-content-center(titulo= "Indicadores de endeudamiento")
        .col-md-12.mb-4.ps-lg-5
          p.mb-5 Miden la capacidad que tiene la empresa para acceder a nuevos crditos y cumplir con los compromisos adquiridos.
          h4.mb-4 Razn de deuda
          p.mb-5 Mide el porcentaje de participacin que tienen los acreedores frente a los activos totales de la empresa.
          .row.mb-4(style='justify-content: center;')
            .col-lg-6
              figure.mb-4
                img(src='@/assets/curso/tema2/formula6.svg', alt='', style='width:540px').mx-auto
              p.mb-4 Se multiplica por 100 con el fin de reflejar el porcentaje.
              figure.mb-4
                img(src='@/assets/curso/tema2/formula7.svg', alt='', style='width:406px').mx-auto
            .col-lg-5
              figure.mb-4
                img(src='@/assets/curso/tema2/img21.svg', alt='imagen ambiental', style='width:520px;').mx-auto
          .cajon.color-acento-contenido.p-4.mb-5
            p.mb-0 #[strong Interpretacin: ] los acreedores de la empresa Contable SAS tienen derecho del 57,09% de sus activos totales.
          
          h4.mb-5 Endeudamiento financiero
          p.mb-4 Mide la participacin de las obligaciones financieras frente a las ventas netas generadas en un perodo determinado.
          p.mb-5 Frmula de endeudamiento financiero:
          .row.mb-4(style='justify-content: center;')
            .col-lg-5
              figure.mb-4
                img(src='@/assets/curso/tema2/img22.png', alt='imagen ambiental', style='width:420px;').mx-auto
            .col-lg-6
              figure.mb-4
                img(src='@/assets/curso/tema2/formula8.svg', alt='', style='width:670px').mx-auto
          .cajon.color-acento-contenido.p-4.mb-5
            p.mb-0 #[strong Interpretacin: ] las obligaciones financieras de la empresa Contable SAS tienen una participacin del 9,6% frente a las ventas netas.
          
          
          
          h4.mb-5 Impacto de la carga financiera 
          p.mb-4 Mide el porcentaje de participacin de los gastos financieros frente a las ventas netas del perodo.
          p.mb-5 Frmula de endeudamiento financiero:
          .row.mb-0(style='justify-content: center;')
            .col-lg-6
              figure.mb-4
                img(src='@/assets/curso/tema2/formula9.svg', alt='Frmula de endeudamiento financiero', style='width:670px').mx-auto
            .col-lg-5
              figure.mb-4
                img(src='@/assets/curso/tema2/img23.png', alt='imagen ambiental', style='width:400px;').mx-auto
          .cajon.color-acento-contenido.p-4.mb-5
            p.mb-0 #[strong Interpretacin: ] los gastos financieros de la empresa Contable SAS representan el 0,40% de las ventas netas.
          
          
          h4.mb-5 Endeudamiento a corto plazo
          p.mb-4 Mide el porcentaje de participacin que tiene el pasivo corriente (a corto plazo) frente al pasivo total, esto quiere decir, que es el porcentaje que tiene vencimiento en un tiempo menor de un ao.
          p.mb-5 Frmula endeudamiento a corto plazo:
          .row.mb-0(style='justify-content: center;')
            .col-lg-5
              figure.mb-4
                img(src='@/assets/curso/tema2/img24.png', alt='imagen ambiental', style='width:360px;').mx-auto
            .col-lg-6
              figure.mb-4
                img(src='@/assets/curso/tema2/formula10.svg', alt='Frmula de Endeudamiento a corto plazo', style='width:670px').mx-auto
          .cajon.color-acento-contenido.p-4.mb-5
            p.mb-0 #[strong Interpretacin: ] el resultado indica que el 100% de los pasivos tiene vencimiento a menos de un ao.
        
        
          h4.mb-5 Razn de cobertura de intereses
          p.mb-4 Mide la incidencia que tienen los gastos financieros sobre las utilidades de la empresa.
          p.mb-5 Frmula de cobertura de intereses:
          .row.mb-0(style='justify-content: center;')
            .col-lg-6
              figure.mb-4
                img(src='@/assets/curso/tema2/formula11.svg', alt='Frmula de cobertura de intereses', style='width:670px').mx-auto
            .col-lg-5
              figure.mb-4
                img(src='@/assets/curso/tema2/img25.png', alt='imagen ambiental', style='width:360px;').mx-auto
          .cajon.color-acento-contenido.p-4.mb-5
            p.mb-0 #[strong Interpretacin: ] la empresa Contable SAS origin una utilidad 69 veces superior al valor de los intereses pagados; por lo tanto, se puede decir que dispone de capacidad para cubrirlos.
        

          h4.mb-5 Indicador de apalancamiento
          p.mb-4 Mide el grado de compromiso de los socios frente a los acreedores de la empresa.
          p.mb-5 Frmula del indicador de apalancamiento:
          .row.mb-0(style='justify-content: center;')
            .col-lg-5
              figure.mb-4
                img(src='@/assets/curso/tema2/img26.svg', alt='imagen ambiental', style='width:400px;').mx-auto
