            p.mb-0 #[strong Interpretacin: ] el resultado indica que la empresa Contable SAS tiene $1,33 de deuda por cada peso de patrimonio.





    h3.mb-4 Indicadores de actividad o rotacin
    p.mb-4 Los indicadores de actividad tambin se les denomina indicadores de rotacin o de eficiencia. Estos indicadores miden la capacidad de la empresa para generar ingresos frente a sus activos.

